<template>
  <div class="full">
    <div class="top clearfix">
      <div class="fl logo">
        {{language.logo}}
      </div>
      <div class="fr logo-btnBox">
        <el-button class="logo-btn" size="medium">
          <i class="iconfont icon-icon-21 logo-icon" style="margin-right:5px"></i>{{language.top_sc}}
        </el-button>
        <el-button class="logo-btn" size="medium">
          <i class="iconfont icon-icon-41 logo-icon" style="margin-right:5px"></i>{{userName}}
        </el-button>
      </div>
    </div>
    <div class="content">
      <div class="left">
        <el-menu class="left-nav" :default-active="$route.path" router 
          background-color="#333c47" text-color="#9fa7b7" active-text-color="#1690ed">
          <el-submenu index="1">
            <template slot="title">
              <i class="iconfont nav-icon icon-icon-"></i>{{language.realTimeMonitoring}}
            </template>
            <el-menu-item index="/main/traffic-monitoring">{{language.trafficMonitoring}}</el-menu-item>
            <el-menu-item index="/main/detailed-data">{{language.DetailedMonitoringData}}</el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">
              <i class="iconfont nav-icon icon-icon-3"></i>监测地址解析
            </template>
            <el-menu-item index="/main/address-resolution">地址解析</el-menu-item>
            <el-menu-item index="/main/address-management">地址管理设置</el-menu-item>
          </el-submenu>
          <el-menu-item index="/main/algorithm-management">
            <i class="iconfont icon-icon-1 nav-icon"></i>监测算法管理
          </el-menu-item>
          <el-menu-item index="/main/tonitoring-analysis">
            <i class="iconfont icon-icon-2 nav-icon"></i>{{language.TonitoringAnalysis}}
          </el-menu-item>
        </el-menu>
      </div>
      <div class="container">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        language: {
          logo: '匿名网络监测分析',
          top_sc: '系统中心',
          realTimeMonitoring: '实时监测解析',
          trafficMonitoring: '流量监测',
          TonitoringAnalysis: '监测信息分析',
          DetailedMonitoringData: '详细监测数据'
        },
        userName: 'admin',
      }
    },
    methods: {
      // 跳轉到其他系統
      handleTurn(url) {
        this.$httpi.get('/wyfx/page/redirect?path=' + url)
          .then(function (response) {
            window.location.href = response.data.msg;
          });
      }
    },
    mounted() {

    }
  }

</script>

<style scoped>
  .top {
    width: 100%;
    height: 70px;
    line-height: 70px;
    background-color: #1690ed;
  }

  .logo {
    font-size: 20px;
    color: #fff;
    font-weight: bold;
    margin-left: 64px;
  }

  .logo-btn {
    border: none;
    background-color: #1690ed;
    color: #fff;
    padding: 10px;
  }

  .logo-btn:hover {
    cursor: pointer;
  }

  .logo-icon {
    color: #fff;
  }

  .logo-btnBox {
    margin-right: 20px;
  }

  .content {
    width: 100%;
    height: calc(100% - 70px);
    position: relative;
  }

  .left {
    width: 230px;
    height: 100%;
  }

  .left-nav {
    width: 100%;
    height: calc(100% - 25px);
    padding-top: 25px;
    background-color: #eeeef6;
  }

  .nav-icon {
    font-size: 24px;
    color: #9fa7b7;
    margin-right: 10px;
  }

  .container {
    position: absolute;
    left: 230px;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: #f0f1f4;
    overflow: auto;
  }

</style>



