<template>
  <div class="main">
    <el-row class="fullHeight" :gutter="10">
      <el-col :span="10" class="fullHeight">
        <div class="card-up my">
          <span class="card-up-txt">
            区域访问排名
          </span>
        </div>
        <div class="card-bottom my">
          <el-row :gutter="10" class="fullHeight">
            <el-col :span="12" class="fullHeight" style="border-right: 2px solid #eeeff2">
              <div class="rank">
                <div class="rank-title">
                  源IP访问量排名
                </div>
                <div class="rank-content">
                  <div class="ce56f85">
                    1. 312.543.6.432 （赵艺宾）
                  </div>
                  <div class="ce56f85">
                    2. 123.44.43.1 （李杰）
                  </div>
                  <div class="ce56f85">
                    3. 31.324.5.9 （龚力）
                  </div>
                  <div>
                    4. 67.3.7.856 （杨坤）
                  </div>
                  <div>
                    5. 142.543.7.67 （邓师放）
                  </div>
                  <div>
                    6. 41.123.34.243 （肖修卓）
                  </div>
                  <div>
                    7. 567.5.2.4 （李江超）
                  </div>
                  <div>
                    8. 51.523.54.4 （孙金光）
                  </div>
                  <div>
                    9. 456.7.52.41 （李文贵）
                  </div>
                  <div>
                    10. 32.52.41.1 （游益锋）
                  </div>
                </div>
              </div>
            </el-col>
            <el-col :span="12" class="fullHeight" style="text-align: right">
              <div class="rank">
                <div class="rank-title">
                  目的地址访问量排名
                </div>
                <div class="rank-content">
                  <div class="cf4a570">
                    1. www.google.com
                  </div>
                  <div class="cf4a570">
                    2. www.google.com
                  </div>
                  <div class="cf4a570">
                    3. www.google.com
                  </div>
                  <div>
                    4. www.google.com
                  </div>
                  <div>
                    5. www.google.com
                  </div>
                  <div>
                    6. www.google.com
                  </div>
                  <div>
                    7. www.google.com
                  </div>
                  <div>
                    8. www.google.com
                  </div>
                  <div>
                    9. www.google.com
                  </div>
                  <div>
                    10. www.google.com
                  </div>
                </div>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-col>
      <el-col :span="14" class="fullHeight">
        <div class="card-up my clearfix">
          <span class="card-up-txt">
            区域访问排名
          </span>
          <el-date-picker class="fr card-up-daterange" v-model="daterange" type="daterange" range-separator="至" start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </div>
        <div class="card-bottom my2">
          <div>
            <el-radio-group v-model="radio" size="small" @change="radioChange">
              <el-radio-button :label="1">总流量</el-radio-button>
              <el-radio-button :label="2">监测总流量</el-radio-button>
              <el-radio-button :label="3">TorHOST流量</el-radio-button>
              <el-radio-button :label="4">TorSSL流量</el-radio-button>
              <el-radio-button :label="5">其他流量</el-radio-button>
            </el-radio-group>
          </div>
          <div id="chart" style="height: calc(100% - 32px)"></div>
        </div>
      </el-col>
    </el-row>

  </div>
</template>

<script>
  import echarts from 'echarts'
  export default {
    data() {
      return {
        daterange: '',
        radio: 1
      }
    },
    methods: {
      radioChange(val) {
        if (val == 1) {
          this.renderDBchart();
        } else {
          this.renderChart();
        }
      },
      renderDBchart() {
        var base = +new Date(1968, 9, 3);
        var oneDay = 24 * 3600 * 1000;
        var date = [];

        var data = [Math.random() * 30];
        var data2 = [Math.random() * 30];

        for (var i = 1; i < 200; i++) {
          var now = new Date(base += oneDay);
          date.push([now.getFullYear(), now.getMonth() + 1, now.getDate()].join('/'));
          data.push(Math.round((Math.random() + 1) * 10000));
        }
        for (var i = 1; i < 200; i++) {
          data2.push(Math.round((Math.random()) * 10000));
        }
        this.chart.clear();
        this.chart.setOption({
          tooltip: {
            trigger: 'axis',
            position: function (pt) {
              return [pt[0], '10%'];
            }
          },
          title: {
            left: 'center',
          },
          toolbox: {
            feature: {
              dataZoom: {
                yAxisIndex: 'none'
              },
              restore: {},
              saveAsImage: {}
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: date
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, '100%']
          },
          dataZoom: [{
            type: 'inside',
            start: 0,
            end: 10
          }, {
            start: 0,
            end: 10,
            handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
            handleSize: '80%',
            handleStyle: {
              color: '#fff',
              shadowBlur: 3,
              shadowColor: 'rgba(0, 0, 0, 0.6)',
              shadowOffsetX: 2,
              shadowOffsetY: 2
            }
          }],
          series: [{
            name: '总流量',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            itemStyle: {
              normal: {
                color: 'rgb(131, 184, 247)'
              }
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: 'rgb(131, 184, 247)'
                }, {
                  offset: 1,
                  color: 'rgb(247, 251, 255)'
                }])
              }
            },
            data: data
          }, {
            name: '匿名访问量',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            itemStyle: {
              normal: {
                color: 'rgb(255, 189, 119)'
              }
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: 'rgb(255, 189, 119)'
                }, {
                  offset: 1,
                  color: 'rgb(255, 251, 246)'
                }])
              }
            },
            data: data2
          }]
        });
      },
      renderChart() {
        var base = +new Date(1968, 9, 3);
        var oneDay = 24 * 3600 * 1000;
        var date = [];

        var data = [Math.random() * 30];

        for (var i = 1; i < 200; i++) {
          var now = new Date(base += oneDay);
          date.push([now.getFullYear(), now.getMonth() + 1, now.getDate()].join('/'));
          data.push(Math.round((Math.random()) * 10000));
        }
        this.chart.clear();
        this.chart.setOption({
          tooltip: {
            trigger: 'axis',
            position: function (pt) {
              return [pt[0], '10%'];
            }
          },
          title: {
            left: 'center',
          },
          toolbox: {
            feature: {
              dataZoom: {
                yAxisIndex: 'none'
              },
              restore: {},
              saveAsImage: {}
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: date
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, '100%']
          },
          dataZoom: [{
            type: 'inside',
            start: 0,
            end: 10
          }, {
            start: 0,
            end: 10,
            handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
            handleSize: '80%',
            handleStyle: {
              color: '#fff',
              shadowBlur: 3,
              shadowColor: 'rgba(0, 0, 0, 0.6)',
              shadowOffsetX: 2,
              shadowOffsetY: 2
            }
          }],
          series: [{
            name: '总流量',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            itemStyle: {
              normal: {
                color: 'rgb(255, 70, 131)'
              }
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: 'rgb(255, 158, 68)'
                }, {
                  offset: 1,
                  color: 'rgb(255, 70, 131)'
                }])
              }
            },
            data: data
          }]
        });
      }
    },
    mounted() {
      this.chart = echarts.init(document.getElementById('chart'));
      this.renderDBchart();
    }
  }

</script>

<style scoped>
  .card-up.my {
    margin-top: 0;
  }

  .card-bottom.my {
    padding: 0;
    width: 100%;
    height: calc(100% - 60px);
  }

  .card-bottom.my2 {
    width: calc(100% - 40px);
    height: calc(100% - 100px);
  }

  .rank {
    padding: 20px;
    width: calc(100% - 40px);
    height: calc(100% - 40px);
  }

  .rank-title {
    font-size: 16px;
  }

  .rank-content {
    width: 100%;
    margin-top: 10px;
    height: calc(100% - 31px);
    overflow: hidden;
    line-height: 40px;
  }

</style>



