import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/pages/Main'
import TrafficMonitoring from '@/pages/TrafficMonitoring'
import TonitoringAnalysis from '@/pages/TonitoringAnalysis'
import DetailedData from '@/pages/DetailedData'
import AddressResolution from '@/pages/AddressResolution'
import AddressManagement from '@/pages/AddressManagement'
import AlgorithmManagement from '@/pages/AlgorithmManagement'

Vue.use(Router)

const router = new Router({
  // html5history模式，返回真实路径需要后台配置
  // mode: 'history',
  routes: [
    { path: "*", redirect: '/main/traffic-monitoring' },
    {
      path: '/main',
      name: 'Main',
      component: Main,
      children: [{
        path: 'traffic-monitoring',
        name: 'TrafficMonitoring',
        component: TrafficMonitoring
      },{
        path: 'tonitoring-analysis',
        name: 'TonitoringAnalysis',
        component: TonitoringAnalysis
      },{
        path: 'detailed-data',
        name: 'DetailedData',
        component: DetailedData
      },{
        path: 'address-resolution',
        name: 'AddressResolution',
        component: AddressResolution
      },{
        path: 'address-management',
        name: 'AddressManagement',
        component: AddressManagement
      },{
        path: 'algorithm-management',
        name: 'AlgorithmManagement',
        component: AlgorithmManagement
      }]
    }
  ]
})

export default router



