<template>
  <div class="main">
    <el-row :gutter="10">
      <el-col :span="4">
        <div class="card">
          <div class="card-mFont c41c977">{{language.monitoring}}</div>
          <div class="card-sFont">{{language.networkStatus}}</div>
        </div>
      </el-col>
      <el-col :span="4">
        <div class="card">
          <div class="card-mFont c1690ed">{{language.synchronizationAlgorithm}}</div>
          <div class="card-sFont">{{language.monitoringAlgorithm}}</div>
        </div>
      </el-col>
      <el-col :span="4">
        <el-row class="card">
          <el-col :span="10">
            <div class="card-flowIcon">
              <img src="../assets/images/card-icon1.png" alt="">
            </div>
          </el-col>
          <el-col :span="14">
            <div>
              <div class="card-mFont cf78a9e">47350</div>
              <div class="card-sFont">{{language.MonitorTotalFlow}}</div>
            </div>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="4">
        <el-row class="card">
          <el-col :span="10">
            <div class="card-flowIcon">
              <img src="../assets/images/card-icon2.png" alt="">
            </div>
          </el-col>
          <el-col :span="14">
            <div>
              <div class="card-mFont c81a9f0">45400</div>
              <div class="card-sFont">{{language.TorHOSTflow}}</div>
            </div>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="4">
        <el-row class="card">
          <el-col :span="10">
            <div class="card-flowIcon">
              <img src="../assets/images/card-icon3.png" alt="">
            </div>
          </el-col>
          <el-col :span="14">
            <div>
              <div class="card-mFont c79cdf7">34400</div>
              <div class="card-sFont">{{language.TorSSLflow}}</div>
            </div>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="4">
        <el-row class="card">
          <el-col :span="10">
            <div class="card-flowIcon">
              <img src="../assets/images/card-icon4.png" alt="">
            </div>
          </el-col>
          <el-col :span="14">
            <div>
              <div class="card-mFont cffac74">32700</div>
              <div class="card-sFont">{{language.otherFlow}}</div>
            </div>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="24">
        <div class="card-up clearfix">
          <span class="card-up-txt">
            {{language.TrafficAnalysis}}
          </span>
          <el-date-picker class="fr card-up-daterange" v-model="daterange" type="daterange" range-separator="至" start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </div>
      </el-col>
    </el-row>
    <div class="card-bottom">
      <div>
        <el-radio-group v-model="radio" size="small" @change="radioChange">
          <el-radio-button :label="1">{{language.MonitorTotalFlow}}</el-radio-button>
          <el-radio-button :label="2">{{language.TorHOSTflow}}</el-radio-button>
          <el-radio-button :label="3">{{language.TorSSLflow}}</el-radio-button>
          <el-radio-button :label="4">{{language.otherFlow}}</el-radio-button>
        </el-radio-group>
      </div>
      <div id="chart" style="height: calc(100% - 32px)"></div>
    </div>
  </div>
</template>

<script>
  import echarts from 'echarts'
  export default {
    data() {
      return {
        language: {
          monitoring: '监测中',
          networkStatus: '网络状况',
          synchronizationAlgorithm: '同步算法',
          monitoringAlgorithm: '监测算法',
          MonitorTotalFlow: '监测总流量',
          TorHOSTflow: 'TorHOST流量',
          TorSSLflow: 'TorSSL流量',
          otherFlow: '其他流量',
          TrafficAnalysis: '流量检测解析'
        },
        userName: 'admin',
        daterange: '',
        chart: '',
        radio: 1
      }
    },
    methods: {
      radioChange() {
        this.renderChart();
      },
      renderChart() {
        var base = +new Date(1968, 9, 3);
        var oneDay = 24 * 3600 * 1000;
        var date = [];

        var data = [Math.random() * 30];

        for (var i = 1; i < 200; i++) {
          var now = new Date(base += oneDay);
          date.push([now.getFullYear(), now.getMonth() + 1, now.getDate()].join('/'));
          data.push(Math.round((Math.random()) * 10000));
        }
        this.chart = echarts.init(document.getElementById('chart'));
        this.chart.setOption({
          tooltip: {
            trigger: 'axis',
            position: function (pt) {
              return [pt[0], '10%'];
            }
          },
          title: {
            left: 'center',
          },
          toolbox: {
            feature: {
              dataZoom: {
                yAxisIndex: 'none'
              },
              restore: {},
              saveAsImage: {}
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: date
          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, '100%']
          },
          dataZoom: [{
            type: 'inside',
            start: 0,
            end: 10
          }, {
            start: 0,
            end: 10,
            handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
            handleSize: '80%',
            handleStyle: {
              color: '#fff',
              shadowBlur: 3,
              shadowColor: 'rgba(0, 0, 0, 0.6)',
              shadowOffsetX: 2,
              shadowOffsetY: 2
            }
          }],
          series: [{
            name: '总流量',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            itemStyle: {
              normal: {
                color: 'rgb(255, 70, 131)'
              }
            },
            areaStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: 'rgb(255, 158, 68)'
                }, {
                  offset: 1,
                  color: 'rgb(255, 70, 131)'
                }])
              }
            },
            data: data
          }]
        });
      }
    },
    mounted() {
      const vm = this;
      this.renderChart();
    }
  }

</script>

<style scoped>


</style>



