        </div>
        <div class="txtBox">
          <span>特征权值二：</span>
          <span>0.12</span>
          <el-input v-model="input13" placeholder="自定义配置" size="small" class="dialogInput"></el-input>
        </div>
        <div class="txtBox">
          <span>特征权值三：</span>
          <span>0.54</span>
          <el-input v-model="input14" placeholder="自定义配置" size="small" class="dialogInput"></el-input>
        </div>
        <div class="txtBox">
          <span>算法版本 v1.0</span>
          <el-button type="text">更新</el-button>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="submit()">保存</el-button>
      </span>
