</script>

<style scoped>
  .card-up.my {
    margin-top: 0;
    width: calc(100% - 20px);
    height: 50px;
    line-height: 40px;
    padding-left: 20px;
  }

  .card-bottom.my {
    padding: 0;
    width: 100%;
    height: calc(100% - 48px);
    overflow: hidden;
  }

  .dialogInput {
    width: 200px;
  }

  .dialogCard {
    margin-top: 30px;
  }

  .txtBox {
    margin-top: 10px;
  }
  .description {
    font-style: italic
  }
</style>



